@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

html{
  scroll-behavior: smooth;
}

body{
  font-family: "Montserrat", sans-serif;
  background-color: #ECF0F3;
}


/* header sticky */
.header--fixed{
  transition: 0.3s;
}
.header--fixed.is-sticky{
  position: fixed;
  top: 0;
  z-index: 999;
  width: 100%;
  transition: 0.3s;
  backdrop-filter: blur(15px);
  background-color: #ECF0F3;
  box-shadow: 10px 10px 19px rgba(0, 0, 0, 0.1);
}
.header--fixed {
  position: absolute;
  top: 0;
  z-index: 999;
  width: 100%;
  transition: 0.3s;
}


.main-header{
  height: 100px;
}
.mainmenu-nav li a{
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 600;
  color: #3c3e41;
}

.main-slider-sec{
  padding-top: 140px;
}

.slider .image-thumbnail {
  position: relative;
  z-index: 2;
}

.slider .image-thumbnail::before {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: calc(100% - 130px);
  content: "";
  left: 50%;
  bottom: 0;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  border-radius: 6px;
  box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
}
.slider .image-thumbnail .inner img {
  border: 0;
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}
.slider .content .inner span.subtitle{  
  display: inline-block;
  letter-spacing: 2px;  
  color: #3C3E41;
  padding: 8px 12px;  
  background: rgba(255, 1, 79, .2);
}

.slider .content .inner .title{
  margin-bottom: 22px;  
  font-size: 52px;
  line-height: 1.2;
  color: #1E2125;
}

.slider .content .inner .title span:nth-child(1){
  color: #FF014F;
}
.cd-words-wrapper{
  color: #1E2125;
}
.slider .content .inner .description{  
  line-height: 30px;
  color: #3C3E41;
  padding-right: 16%;
  opacity: 0.9;
  font-family: "Poppins", sans-serif;
}

.effect-1 {
  animation: rotate360 40s linear infinite reverse;
  bottom: -100px;
  right: 0;
  position: absolute;
  z-index: -1;
}
@keyframes rotate360 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* features section */
.rn-service{
  text-align: left;
  padding: 35px 50px 35px 50px;
  transition: 0.5s all ease-in-out;
  position: relative;
  border-radius: 10px;
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
  position: relative;
  z-index: 1;
  height: 100%;
  cursor: pointer;
}
.rn-service::before {
  content: "";
  position: absolute;
  transition: 0.4s;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  top: 0;
  left: 0;
  background: linear-gradient(145deg, #ff014f, #d11414);
  opacity: 0;
  z-index: -1;
}
.rn-service:hover::before {
  opacity: 1;
}
.rn-service .inner > * {
  transition: 700ms cubic-bezier(0.17, 0.67, 0, 1.01);
  transform: translateY(20px);
}
.rn-service:hover .inner > * {
  transform: translateY(0px);
}
.rn-service:hover .inner .content .title {  
  color: #ffffff;
}
.rn-service .inner .content .title {  
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  margin-bottom: 20px;
  color: #1E2125;
  font-family: "Poppins", sans-serif;
}
.rn-service .inner .content .title a{
  transition: 0.4s;
}
.rn-service .inner .content p.description {
  font-size: 16px;
  line-height: 28px;
  color: #3c3e41;
  margin-bottom: 22px;
  transition: 0.4s;
  font-family: "Poppins", sans-serif;
}
.rn-service:hover .inner .content p.description {
  color: #ffffff;
}

.section-title span.subtitle {
  color: #f9004d;
  letter-spacing: 2px;
  display: block;
  line-height: 14px;
  font-weight: 500;
  text-transform: uppercase;
} 
.section-title h2.title {
  font-size: 60px;
  font-weight: 700;
  line-height: 1.2;
  color: #3c3e41;
  margin-top: 15px;
  margin-bottom: 0;
}


/* project slider */
.project-portfolio{
  border-radius: 20px;
  z-index: 1;
  height: 100%;
  transition: 0.5s all ease-in-out;
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
}

.project-portfolio:hover .thumbnail-image img {
  transform: scale(1.1);
}
.project-portfolio .thumbnail-image img {
  transition: 0.4s;
}